/* Edited for Manor Theme */

// Spacing variables
//
// Styleguide 1.3

// Padding variables
//
// For ease of use, calculations have been set at variable definition level.
// Level then equals to multiplying factor. If you have a base of 5px and you need a 15px padding you´ll choose the $sc-padding-lv3 variable.
//
// $sc-padding-base - Usually set to 5px but may be specified differently.
// $sc-padding-lv1 - Level 1 equals to base padding.
// $sc-padding-lv2 - Level 2 equals to base padding by 2.
// $sc-padding-lv3 - Level 3 equals to base padding by 3.
// $sc-padding-lv4 - Level 4 equals to base padding by 4.
// $sc-padding-lv5 - Level 5 equals to base padding by 5.
// $sc-padding-lv6 - Level 6 equals to base padding by 6.
// $sc-padding-lv7 - Level 7 equals to base padding by 7.
// $sc-padding-lv8 - Level 8 equals to base padding by 8.
// $sc-padding-lv9 - Level 9 equals to base padding by 9.
// $sc-padding-lv10 - Level 10 equals to base padding by 10.
//
// Markup:
// <div class="styleguide-padding-lv1"><span>sc-padding-lv1</span></div>
// <div class="styleguide-padding-lv2"><span>sc-padding-lv2</span></div>
// <div class="styleguide-padding-lv3"><span>sc-padding-lv3</span></div>
// <div class="styleguide-padding-lv4"><span>sc-padding-lv4</span></div>
// <div class="styleguide-padding-lv5"><span>sc-padding-lv5</span></div>
// <div class="styleguide-padding-lv6"><span>sc-padding-lv6</span></div>
// <div class="styleguide-padding-lv7"><span>sc-padding-lv7</span></div>
// <div class="styleguide-padding-lv8"><span>sc-padding-lv8</span></div>
// <div class="styleguide-padding-lv9"><span>sc-padding-lv9</span></div>
// <div class="styleguide-padding-lv10"><span>sc-padding-lv10</span></div>
//
// Styleguide 1.3.1
$sc-padding-base: 5px;
$sc-padding-lv1: $sc-padding-base;
$sc-padding-lv2: $sc-padding-base * 2;
$sc-padding-lv3: $sc-padding-base * 3;
$sc-padding-lv4: $sc-padding-base * 4;
$sc-padding-lv5: $sc-padding-base * 5;
$sc-padding-lv6: $sc-padding-base * 6;
$sc-padding-lv7: $sc-padding-base * 7;
$sc-padding-lv8: $sc-padding-base * 8;
$sc-padding-lv9: $sc-padding-base * 9;
$sc-padding-lv10: $sc-padding-base * 10;

// Margin variables
//
// For ease of use, calculations have been set at variable definition level.
// Level then equals to multiplying factor. If you have a base of 5px and you need a 15px margin you´ll choose the $sc-margin-lv3 variable.
//
// $sc-margin-base - Usually set to 5px but may be specified differently.
// $sc-margin-lv1 - Level 1 equals to base margin.
// $sc-margin-lv2 - Level 2 equals to base margin by 2.
// $sc-margin-lv3 - Level 3 equals to base margin by 3.
// $sc-margin-lv4 - Level 4 equals to base margin by 4.
// $sc-margin-lv5 - Level 5 equals to base margin by 5.
// $sc-margin-lv6 - Level 6 equals to base margin by 6.
// $sc-margin-lv7 - Level 7 equals to base margin by 7.
// $sc-margin-lv8 - Level 8 equals to base margin by 8.
// $sc-margin-lv9 - Level 9 equals to base margin by 9.
// $sc-margin-lv10 - Level 10 equals to base margin by 10.
//
// Markup:
// <div class="styleguide-margin-lv1"><span>sc-margin-lv1</span></div>
// <div class="styleguide-margin-lv2"><span>sc-margin-lv2</span></div>
// <div class="styleguide-margin-lv3"><span>sc-margin-lv3</span></div>
// <div class="styleguide-margin-lv4"><span>sc-margin-lv4</span></div>
// <div class="styleguide-margin-lv5"><span>sc-margin-lv5</span></div>
// <div class="styleguide-margin-lv6"><span>sc-margin-lv6</span></div>
// <div class="styleguide-margin-lv7"><span>sc-margin-lv7</span></div>
// <div class="styleguide-margin-lv8"><span>sc-margin-lv8</span></div>
// <div class="styleguide-margin-lv9"><span>sc-margin-lv9</span></div>
// <div class="styleguide-margin-lv10"><span>sc-margin-lv10</span></div>
//
// Styleguide 1.3.2
$sc-margin-base: 5px;
$sc-margin-lv1: $sc-margin-base;
$sc-margin-lv2: $sc-margin-base * 2;
$sc-margin-lv3: $sc-margin-base * 3;
$sc-margin-lv4: $sc-margin-base * 4;
$sc-margin-lv5: $sc-margin-base * 5;
$sc-margin-lv6: $sc-margin-base * 6;
$sc-margin-lv7: $sc-margin-base * 7;
$sc-margin-lv8: $sc-margin-base * 8;
$sc-margin-lv9: $sc-margin-base * 9;
$sc-margin-lv10: $sc-margin-base * 10;

// Content variables
//
// The content is set to span a width of 100%, yet the content doesn't always touch the edges.
// These variables supply small left and right padding to the content area.
//
// Styleguide 1.3.3
$sc-content-left-padding-desktop: 50px; /* editable({"type": "string", "label": "Header left padding"}) */
$sc-content-right-padding-desktop: 20px; /* editable({"type": "string", "label": "Header right padding"}) */
$sc-content-left-padding: 15px; /* editable({"type": "string", "label": "General left padding"}) */
$sc-content-right-padding: 15px; /* editable({"type": "string", "label": "General right padding"}) */
